import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
import {KsImage} from 'components/ks-components/image/Image'
import {UniPopup} from 'components/uni/uni-popup/uni-popup'
import {KsEmpty} from 'components/ks-components/empty/empty'
import {KsTabs} from 'components/ks-components/tabs/tabs'
import {GetUserCourseOrderList,InitialUserPayCourse,DoUserPayCourse,DeleteUserCourseOrder} from 'api/user'
export default {
	components:{
		UniPopup,
		KsEmpty,
		KsTabs,
		KsImage,
		UniLoadMore
	},
	data() {
		return {
			userInfo:{},
			index:0,
			tabList:[
				{value:-1,name:'全部'},
				{value:1,name:'已付款'},
				{value:0,name:'未付款'}
			],
			list:[],
			status:-1,
			page:1,
			pageCount:0,
			moreStatus:"",
			payData:{},
			price:'',
			show:false,
			money:0,
			paymentList:[],
			activePay:-1,
			payId:0,
			id:''
		}
	},
	
	/*滚动加载*/
	onReachBottom(){
		this.page++
		if(this.page<=this.pageCount){
			if(this.index == 0){
				this.getList(this.page)
			}else{
				this.getAskList(this.page)
			}				
		}	
	},
	onLoad() {
		this.$store.ready(state=>{
			this.userInfo = state.userInfo
			this.getList()
		})	
	},
	onPullDownRefresh() {
		this.getList() 
	},
	methods: {
		changeList(e){
			this.status = e.tab.value
			this.getList()
		},
		open(item){
			if(item.costype == '社群'){
				uni.navigateTo({
					url:'/pages/class-center/group/details?boardid='+item.courseid
				})
			}else{
				var url = item.view_url
				url = encodeURIComponent(url)
				uni.navigateTo({
					url:'/pages/web/web?title='+item.coursename+'&photo='+item.photourl+'&url='+url
				})
			}
			
		},
		/**请求课程订单数据列表*/
		getList(p=1){
			this.page = p
			if(this.page==1){
				this.list = []
			}
			this.moreStatus = 'loading'
			GetUserCourseOrderList({
				show: this.status,
				page:this.page,
				pagesize:10,
			}).then(res=>{
				uni.stopPullDownRefresh();
				this.pageCount = res.pagecount
				if(this.page >= this.pageCount){
					this.moreStatus = 'noMore'
				}else{
					this.moreStatus = 'more'
				}
				if(res.result){
					this.list = this.list.concat(res.data)
				}
			})		
		},
		pay(item){
			uni.navigateTo({
				url:'/pages/payment/payment?orderid='+item.orderid
			})	
		},
		
		/**删除课程订单
		 */
		deleteUserCourseOrder(item) {
			var that = this
			uni.showModal({
			    title: '提示',
			    content: '确认删除该订单吗？',
			    success:function (res) {
			        if (res.confirm) {
						DeleteUserCourseOrder({
						    ids: item.id,
						}).then(res=> {
						    if (res.result) {
						        that.getList()
						    }else{
								uni.showToast({
									title: res.msg,
									icon:'none'
								}); 
							}
						})
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		}
	}
}
